  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.caption {
  color: #999;
}
.text-ellipses {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.field-display-value {
  color: #fff;
}
.table-expanded-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
.expanded-table-row {
  background: #333;
  padding-top: 10px;
  padding-bottom: 10px;
}
.table-expanded-item p {
  margin-bottom: 0px;
}
.v-data-table__wrapper::-webkit-scrollbar {
  width: 10px;
}
.v-data-table__wrapper::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}
.v-data-table__wrapper::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}
.v-data-table__wrapper::-webkit-scrollbar-thumb:hover {
  background: white;
}
